.discover {
  .paginator {
    margin: 0 auto;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    button + button {
      margin-left: 5px;
    }
  }
  .grid {
    display: flex;
    flex-wrap: wrap;
    padding: 15px;
    justify-content: space-between;
    .card {
      width: 250px;
      cursor: pointer;
      transition: 0.5s;
      &:hover {
        transform: scale(1.2);
        z-index: 1;
      }
    }
  }
}